import * as React from 'react';
import {
    Box,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Stack,
    Button, Grid
} from "@mui/material";
import LinkIcon from '@mui/icons-material/Link';
import TwitterIcon from '@mui/icons-material/Twitter';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

import { Paper } from "../../components/Paper";

function createData(name, value) {
    return { name, value };
}

const rows = [
    createData('Circulating supply', 6000),
    createData('Volume', '$1.19M'),
    createData('Owners', 4839),
];

const rowsCHMN = [
    createData('Circulating supply', '498,318,038'),
    createData('Volume 24h', '$1.19M'),
    createData('Transactions 24h', '$48.3M'),
    createData('Market cap', '$474.58M'),
];

const links = [
    {
        icon: LinkIcon,
        text: 'chainmyth.io',
        link: 'https://twitter.com/chainmyth'
    },
    {
        icon: TwitterIcon,
        text: '@chainmyth',
        link: 'https://twitter.com/chainmyth'
    },
    {
        icon: AccountCircleIcon,
        text: '/49jd8d08',
        link: 'https://twitter.com/chainmyth'
    }
]

export const Info = () => {
    return (
        <Paper
            sx={{
                p: 2,
            }}
        >
            <Stack spacing={4}>
                <Box>
                    <Typography sx={{ fontSize: (theme) => theme.spacing(2) }} color="text.secondary">
                        NFT floor
                    </Typography>
                    <Typography sx={{ fontSize: (theme) => theme.spacing(2) }}>
                        0.012 SOL
                    </Typography>

                    <Table aria-label="simple table">
                        <TableBody>
                            {rows.map((row) => (
                                <TableRow
                                    key={row.name}
                                >
                                    <TableCell component="th" scope="row" sx={{color: 'rgb(137, 137, 137)'}}>
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="right">{row.value}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Box>

                <Box>
                    <Typography sx={{ fontSize: (theme) => theme.spacing(2) }} color="text.secondary">
                        $CHMN price
                    </Typography>
                    <Typography sx={{ fontSize: (theme) => theme.spacing(2) }}>
                        0.972 SOL
                    </Typography>

                    <Table aria-label="simple table">
                        <TableBody>
                            {rowsCHMN.map((row) => (
                                <TableRow
                                    key={row.name}
                                >
                                    <TableCell component="th" scope="row" sx={{color: 'rgb(137, 137, 137)'}}>
                                        {row.name}
                                    </TableCell>
                                    <TableCell align="right">{row.value}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Box>

                <Grid container spacing={1}>
                    {links.map(({text, link, icon: Icon}) => <Grid item key={text}>
                        <Button
                            variant={"contained"} color={"secondary"} sx={{
                            textTransform: 'none',
                        }} startIcon={<Icon/>} target={"_blank"} href={link}>
                            {text}
                        </Button>
                    </Grid>)}
                </Grid>
            </Stack>
        </Paper>
    );
}