import * as React from 'react';
import { Toolbar as MuiToolbar, Link, Typography } from "@mui/material";
import { Search } from "../../components/Search";

export const Toolbar = () => {
    return (
        <MuiToolbar>
            <Typography noWrap>
                Glip NFT
            </Typography>

            <Search/>

            <nav>
                <Link
                    underline="none"
                    color="text.primary"
                    href="#"
                    sx={{ my: 1, mx: 1.5 }}
                >
                    Games
                </Link>

                <Link
                    underline="none"
                    color="text.primary"
                    href="#"
                    sx={{ my: 1, mx: 1.5 }}
                >
                    Guilds
                </Link>
            </nav>
        </MuiToolbar>
    );
}