import * as React from 'react';
import { Paper } from "../../components/Paper";
import {Button} from "../../components/Button";
import {Grid, Card, CardContent, CardActions, CardMedia, Typography, Stack, Box} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";

import Monkey1 from "../../static/monkey1.png";
import Monkey2 from "../../static/monkey2.jpg";
import Monkey3 from "../../static/monkey3.jpg";
import Monkey4 from "../../static/monkey4.jpg";
import Monkey5 from "../../static/monkey5.png";

const CARDS = [
    {
        title: '8.99 MATIC',
        description: 'Small Apartment 4740',
        src: Monkey1,
    },
    {
        title: '20.99 MATIC',
        description: 'CloneX #12359',
        src: Monkey2,
    },
    {
        title: '89 MATIC',
        description: 'Small Apartment 4741',
        src: Monkey3,
    },
    {
        title: '9.99 MATIC',
        description: 'CloneX #1229',
        src: Monkey4,
    },
    {
        title: '4.99 MATIC',
        description: 'CloneX #1956',
        src: Monkey5,
    },
    {
        title: '9.54 MATIC',
        description: 'CloneX #7107',
        src: Monkey2,
    },
];

export const How2Earn = () => {
    return (
        <Paper sx={{ p: 4 }}>
            <Stack spacing={4}>
                <Grid container spacing={2}>
                    {CARDS.map((layout) => (
                        <Grid item sm={6} md={4} sx={{ flexGrow: 1 }} key={layout.description}>
                            <Card
                                sx={{
                                    height: '100%',
                                    display: 'flex',
                                    flexDirection: 'column',
                                    flexGrow: 1,
                                }}
                                elevation={5}
                            >
                                <CardMedia
                                    component="a"
                                    href={layout.href}
                                    sx={{ height: 0, pt: '100%' }}
                                    image={layout.src}
                                    title={layout.title}
                                    rel="nofollow"
                                    target="_blank"
                                />
                                <CardContent sx={{ flexGrow: 1 }}>
                                    <Typography gutterBottom variant="subtitle2" component="h2" color={"primary.main"}>
                                        {layout.title}
                                    </Typography>
                                    <Typography variant="subtitle1">{layout.description}</Typography>
                                </CardContent>

                                <CardActions sx={{justifyContent: 'center', background: 'grey'}}>
                                    Buy
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                </Grid>

                <Box sx={{textAlign: 'center'}}>
                    <Button sx={{width: '340px'}} endIcon={<SendIcon />}>
                        Go to market
                    </Button>
                </Box>
            </Stack>
        </Paper>
    );
}