import { createTheme } from "@mui/material";
import JetBrainsMono from './fonts/JetBrainsMono-Regular.woff2';

console.log(JetBrainsMono)
export const darkTheme = createTheme({
    palette: {
        primary: {
            // light: will be calculated from palette.primary.main,
            main: '#CCFF3D',
            // dark: will be calculated from palette.primary.main,
            // contrastText: will be calculated to contrast with palette.primary.main
        },
        secondary: {
            main: '#575757',
        },
        mode: 'dark',
    },
    components: {
        MuiCssBaseline: {
            styleOverrides: `
                @font-face {
                  font-family: 'JetBrainsMono';
                  font-style: normal;
                  font-display: swap;
                  font-weight: 400;
                  src: local('JetBrainsMono'), 
                  url(${JetBrainsMono}) format('woff2');
                }
              `,
        },
    },
    typography: {
        fontFamily: 'JetBrainsMono, Arial',
    },
});