import React, { useState, useRef } from 'react';
import { Grid, Stack, Typography, Chip, TextField, Rating, styled } from "@mui/material";
import { Paper } from "../../components/Paper";
import { Button } from "../../components/Button";
import StarRateIcon from '@mui/icons-material/StarRate';

const INIT_REVIEWS = [
    {
        rating: 1,
        text: "Sifu's unique aging mechanic and top-tier combat make the journey from a headstrong student to a wise kung fu master utterly thrilling."
    },
    {
        rating: 4,
        text: "Sifu fucking sucks LMFAO"
    },
    {
        rating: 3,
        text: "Gay fucker. You suck. I am tranrace homophobe from outer space who likes jayz"
    }
]

const StyledRating = styled(Rating)(({ theme }) => ({
    '& .MuiRating-iconFilled': {
        color: theme.palette.primary.main,
    },
    '& .MuiRating-iconHover': {
        color: theme.palette.primary.light,
    },
}));

export const Review = () => {
    const inputRef = useRef(undefined);
    const [reviews, setReviews] = useState(INIT_REVIEWS);
    const [rating, setRating] = useState(0);

    const getAverageRating = () => {
        return (reviews.reduce(
            (total, next) => total + next.rating, 0
        ) / reviews.length).toFixed(1)
    }

    const addPost = () => {
        setReviews([...reviews, {
            rating,
            text: inputRef.current.value
        }]);
        inputRef.current.value = ''
        setRating(0);
    }

    return (
        <Grid container spacing={2}>
            <Grid item xs={12}>
                <Stack direction="row" alignItems="center" spacing={2}>
                    <Typography variant={"h4"}>{getAverageRating()}</Typography>
                    <StarRateIcon/>
                    <Chip label={`${reviews.length} Reviews`} sx={{borderRadius: 0}}/>
                </Stack>
            </Grid>
            <Grid item xs={12} sx={{position: 'relative'}}>
                <TextField
                    id="outlined-multiline-static"
                    multiline
                    rows={4}
                    placeholder="Enter review here..."
                    fullWidth
                    color={"secondary"}
                    inputRef={inputRef}
                    InputProps={{
                        sx: {borderRadius: 0}
                    }}

                />
                <Stack direction="row" alignItems="center" spacing={2} sx={{
                    position: 'absolute',
                    bottom: 5,
                    right: 10
                }}>
                    <Typography variant={"button"} color={'secondary.main'}>CHOOSE RATING</Typography>
                    <StyledRating
                        size="large"
                        value={rating}
                        onChange={(event, newValue) => {
                            setRating(newValue);
                        }}
                    />
                    <Button disabled={!rating} onClick={addPost}>Post</Button>
                </Stack>
            </Grid>
            {
                reviews.map(({rating, text}) => <Grid item xs={12} key={text}>
                    <Paper sx={{ p: 4 }}>
                        <Stack direction="row" alignItems="center" spacing={4}>
                            <Typography variant={"h4"} color={'primary.main'}>{`${rating}/5`}</Typography>
                            <Typography>{text}</Typography>
                        </Stack>
                    </Paper>
                </Grid>)
            }
        </Grid>
    );
}