import * as React from 'react';
import { Button as MUIButton } from "@mui/material";

export const Button = ({children, sx,...props}) => {

    return (
        <MUIButton variant="contained" sx={{
            ...sx,
            position: 'relative',
            borderRadius: 0,
            textTransform: 'none',
            overflow: 'hidden',
            '&:before, &:after': {
                height: (theme) => theme.spacing(1),
                width: (theme) => theme.spacing(1),
                background: (theme) => theme.palette.background.default,
                position: 'absolute',
                content: '""',
            },
            '&:before': {
                top: '0',
                left: '0',
                transform: 'rotate(50deg) translate(-71%)'
            },
            '&:after': {
                bottom: '0',
                right: '0',
                transform: 'rotate(50deg) translate(71%)'
            },
        }} {...props}>
            {children}
        </MUIButton>
    );
}