import * as React from 'react';
import { Tabs as MuiTabs, Tab, Box, Collapse } from '@mui/material';

import { Overview } from "../Overview";
import { About } from "../About";
import { Team } from "../Team";
import { How2Earn } from "../How2Earn";

const CONTENT_MAP = [
    {
        title: 'Overview',
        comp: Overview
    },
    {
        title: 'About',
        comp: About
    },
    {
        title: 'Team & Roadmap',
        comp: Team
    },
    {
        title: 'How2Earn',
        comp: How2Earn
    }
]

export const Tabs = () => {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ width: '100%' }}>
            <MuiTabs value={value} onChange={handleChange}>
                {
                    CONTENT_MAP.map(({title}) => (
                        <Tab key={title} label={title}/>
                    ))
                }
            </MuiTabs>

            {
                CONTENT_MAP.map(({title, comp: Component}, index) => (
                    <Collapse key={title} in={value === index} timeout={2000}>
                        <Component/>
                    </Collapse>
                ))
            }
        </Box>
    );
}