import * as React from 'react';
import {Button as MuiButton, ButtonGroup, Grid, Stack, Typography } from "@mui/material";
import LinkIcon from "@mui/icons-material/Link";

import logoImg from "../../styles/assets/img.png";

import { Button } from "../../components/Button";

export const Title = () => {
    return (
        <Grid container item xs={12} spacing={2}>
            <Grid item xs={12} md={8}>
                <Stack direction="row" alignItems="center" spacing={1}>
                    <img src={logoImg} width={54}/>
                    <Typography variant={"h3"}>ChainMyth</Typography>
                    <Typography color="text.secondary" sx={{opacity: 0.4}}>$CHMN</Typography>
                </Stack>

                <ButtonGroup disabled size="small" variant={"contained"} color={"secondary"}>
                    <MuiButton startIcon={<LinkIcon/>} sx={{borderRadius: 0, textTransform: 'none'}}>chainmyth.io</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Free2Play</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Solana</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Star 4.5</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Action</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Adventure</MuiButton>
                    <MuiButton sx={{borderRadius: 0, textTransform: 'none'}}>Doxxed</MuiButton>
                </ButtonGroup>
            </Grid>

            <Grid item xs={12} md={4}>
                <Stack spacing={2}>
                    <Button fullWidth>Go to market</Button>

                    <Stack direction="row" spacing={1}>
                        <Button fullWidth variant="outlined" color={"secondary"}>Follow</Button>
                        <Button fullWidth variant="outlined" color={"secondary"}>Play</Button>
                    </Stack>
                </Stack>
            </Grid>
        </Grid>
    );
}